* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.search-form {
  background-color: #3f51b5;
  justify-content: center;
  padding: 20px;
  display: flex;
}

.search-form input[type="text"] {
  width: 50%;
  border: none;
  border-radius: 4px;
  padding: 10px;
  font-size: 20px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #303f9f;
  border: none;
  border-radius: 4px;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 20px;
}

.search-form button:hover {
  background-color: #283593;
}

.gallery {
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin-top: 20px;
  padding: 20px;
  display: grid;
}

.photo-card {
  background-color: #fff;
  border-radius: 8px;
  transition: transform .3s;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.photo-card:hover {
  transform: translateY(-10px);
}

.photo-card img {
  width: 100%;
  height: 250px;
  object-fit: cover;
}

.info {
  text-align: center;
  justify-content: space-between;
  padding: 10px;
  display: flex;
}

.info-item {
  color: #333;
  flex: 1;
  margin: 5px 0;
  font-size: 14px;
}

.info-item b {
  margin-bottom: 5px;
  font-size: 16px;
  display: block;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #3f51b5;
  border: none;
  border-radius: 4px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 20px;
  transition: background-color .3s, transform .2s;
  display: block;
}

.load-more:hover {
  background-color: #303f9f;
  transform: translateY(-3px);
}

.load-more:disabled {
  cursor: not-allowed;
  background-color: #c1c1c1;
}

.hidden {
  display: none;
}

